.sidebarOption {
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 10px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    cursor: pointer;
    color: #818181;

    .MuiSvgIcon-root {
        padding: 5px;
    }

    h3 {
        flex: 1;
        margin-left: 10px;
        font-size: 14px;
        font-weight: 400;

    }

    p {
        display: none;
        font-weight: 300;
    }

    &:hover,
    &:hover>h3,
    &:hover>p,
    &--active,
    &--active>p,
    &--active>h3 {
        background-color: #fcecec;
        color: #c04b37;
        font-weight: 800 !important;
    }

    &:hover>p {
        display: inline;
    }

    &--active>p {
        display: inline;
    }
}